.search {
    margin: auto;
    /*border: 1px solid white;*/
    /*display: grid;*/
    /*grid-template-columns: 1fr;*/
    /*background: white;*/
    /*height: 40px;*/
    /*width: 200px;*/
}
.searchInputs {
    display: grid;
    grid-template-columns: auto 1fr;
    background: #fff;
}

.search input {
    background-color: white;
    border: 0;
    border-radius: 2px 0px 0px 2px;
    font-size: 18px;
    padding: 15px;
    height: 30px;
    width: 300px;
}

input:focus {
    outline: none;
}
.searchIcon svg {
    font-size: 35px;
    color: black;
}

.dataResult {
    margin-top: 5px;
    width: 300px;
    height: 200px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
    display: none;
}



.dataResult .dataItem {
    width: 300px;
    height: 50px;
    display: flex;
    align-items: center;
    color: black;
}

.dataItem p {
    margin-left: 10px;

}
a {
    text-decoration: none;
}

a:hover {
    background-color: lightgrey;
}

#clearBtn {
    cursor: pointer;
    color: black;
}